/* smaller resolution styles */

@media (min-width: 100px) and (max-width: 500px) {

  .fa-bars {
    display: block !important;
  }

  .header-links {
    display: none;
  }
}

/* mobile styles */

@media (min-width: 100px) and (max-width: 400px) {

  .content {
    max-width: 300px;
  }

  .aboutme h1 {
    font-size: 28px;
  }

}